/* Header CSS*/

/*        ------------------
         | NON-MOBILE DEVS |
         -------------------
*/

.mainDiv {
  backgroundColor: 'black';
  opacity: 0.9;
  filter: alpha(opacity=0);
  padding-bottom: auto;
  /* position: fixed; */
}


#mobileDrop {
  align: right;
  padding-left: 5px;

}

#GitLogo {
  width: 7vh;
  height: 7vh;
  padding: 10px;
  margin-left: -4.4vw;
  -webkit-filter: brightness(0) invert(0);
          filter: brightness(0) invert(0);
}

#GitLogo:hover {
  -webkit-filter: brightness(0) invert(0.5);
          filter: brightness(0) invert(0.5);
}

#linkedLogo {
  width: 7vh;
  height: 7vh;
  padding: 10px;
  /* padding-right: 1vw; */
  -webkit-filter: brightness(0%) invert(0);
          filter: brightness(0%) invert(0);
}

#linkedLogo:hover {
  -webkit-filter: brightness(0) invert(0.5);
          filter: brightness(0) invert(0.5);
}

#codepen {
  width: 7vh;
  height: 7vh;
  padding: 10px;
  /* padding-right: 1vw; */
  -webkit-filter: brightness(0%) invert(0);
          filter: brightness(0%) invert(0);
}

#codepen:hover {
  -webkit-filter: brightness(0) invert(0.5);
          filter: brightness(0) invert(0.5);
}

